import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
export default
    {
        data() {
            return {
                balance: 123123312,
                info_form:
                {
                    recharge_money: 50
                }
            }
        },
        methods:
        {
            //确认充值
            confirm(type) {

                var that = this;

                if (this.info_form.recharge_money == undefined || parseFloat(this.info_form.recharge_money) <= 0 || this.info_form.length == 0) {
                    MessageBox.alert('金额不可低于0元', '提示');
                    return;

                }
                if(type == 1)
                {
                     $http.get('finance.balance.recharge', { pay_type: type, recharge_money: this.info_form.recharge_money }, '正在充值...').then(function (response) {
                    
                    if (response.result == 1) {
                        if (type == 1) {
                            //微信充值
                            console.log(response.data.js);
                            wx.config(response.data.js);
                            that.WXPay(response.data.config);
                        } else {
//params:{alipay_url:response.data}
                            
                            //window.location.href = response.data;
                            //MessageBox.alert('请您打开手机浏览器完成充值', '提示');
                            //return;

                        }



                    } else {
                        MessageBox.alert(response.msg, '提示');
                    }
                }, function (response) {
                    // error callback
                });

            }else
            {
                that.$router.push({name:'alipay',params:{recharge_money: this.info_form.recharge_money}});

            }
               



            },
            WXPay(payParams) {
                //alert('"'+payParams.timestamp+'"');
                var that = this;
                wx.chooseWXPay({
                    appId: payParams.appId,
                    timestamp: payParams.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    nonceStr: payParams.nonceStr, // 支付签名随机串，不长于 32 位
                    package: payParams.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                    signType: payParams.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    paySign: payParams.paySign, // 支付签名
                    success: function (res) {
                        // 支付成功后的回调函数
                        if (res.errMsg == "chooseWXPay:ok") {
                            MessageBox.alert(response.msg, '提示');
                            this.$router.go(-1);
                        } else {
                            alert('充值失败，请返回重试');
                        }
                    },
                    cancel: function (res) {
                        //支付取消
                    },
                    fail: function (res) {

                        alert("充值失败，请返回重试。");
                    }
                });


            }
        },
        activated() {
            this.balance = this.$route.params.balance;
        },
        components: { cTitle }
    }  


